<script>
import TableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";
import config from "../../../../public/config/customSetting.json";

export default {
  name: "download-component",
  components: { TableComponent, Pagination1 },
  data() {
    return {
      form: { dType: "" },
      rules: {
        dType: [{ required: true, message: "必填", trigger: "change" }],
      },

      allFileTableColumns: [
        { id: "index", label: "項次", width: "80" },
        { id: "d_File_Name", label: "文件名稱", width: "" },
      ],
    };
  },
  computed: {
    // 文件類型總清單
    fileTypeList() {
      return this.$store.state.fileType_list;
    },
    // 文件資料 (All)
    allFile() {
      return this.$store.state.allFile;
    },
    // 文件資料 (單頁)
    allFileList() {
      return this.$store.state.allFile_list;
    },
  },
  methods: {
    // 查詢相應的文件檔案，呈現表格第一頁
    async qryAllFiles() {
      try {
        await this.$store.dispatch("getAllFiles", this.form.dType);
        // this.$refs.form.resetFields();
        this.$store.commit("CURRENT_PAGE_INIT");
      } catch (err) {
        console.log(err);
      }
    },
    // 從對應 controller 下載文件，並指定檔名
    async downloadPdf(item) {
      try {
        const res = await this.$store.dispatch("commonDownloadPdf", {
          file_id: item.d_Id,
          router_Name: config.getFileMgmt,
        });
        this.downloadFile(res, item.d_File_Name, "pdf");
      } catch (err) {
        console.log(err);
      }
    },

    changePage(page) {
      this.$store.commit("CHANGE_PAGE_HANDLER", {
        page,
        paramsDataName: "allFile",
        paramsListName: "allFile_list",
      });
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    try {
      // 清空先前的查詢結果
      this.$store.state.allFile = [];
      this.$store.state.allFile_list = [];

      // 打 API 取得文件種類
      await this.$store.dispatch("getFileType");
    } catch (err) {
      console.log(err);
    }
    this.$store.commit("CURRENT_PAGE_INIT");
  },
};
</script>

<template src="./FileDL.html"></template>
